import React, { useContext, useEffect, useMemo } from "react";
import styles from "./Result.scss";
import { MyStatusContext } from "./index.jsx";

/**
    things 统计的物品数组
    result 属性结果统计
 */

export default function Result(props) {
  const { things } = props;
  // MyStatusContext 共享全局状态, 相当于不断添补 redux 的一个属性 state
  const [, setState] = useContext(MyStatusContext);
  const status = ["att", "matt", "str", "dex", "int", "luk"];

  // result 缓存属性的统计结果, thingName 物品名称 (在复杂计算时使用, 其他情况不如直接定义)
  const [result, thingName] = useMemo(() => {
    const status = ["att", "matt", "str", "dex", "int", "luk"];
    let thingName = "";
    let result = {
      str: { min: null, max: null },
      dex: { min: null, max: null },
      int: { min: null, max: null },
      luk: { min: null, max: null },
      att: { min: null, max: null },
      matt: { min: null, max: null },
    };

    const _things = things;
    _things &&
      _things.map((thing) =>
        status.map((status) => {
          if (thing.id === "level") {
            result[status].max += thing[status].max * 5;
            result[status].min += thing[status].min * 5;
          } else if (thing.id === "scroll-9") {
            result[status].max += thing[status].max * 9;
            result[status].min += thing[status].min * 9;
          } else if (thing.id === "scroll-12") {
            result[status].max += thing[status].max * 12;
            result[status].min += thing[status].min * 12;
          } else {
            result[status].max += thing[status].max;
            result[status].min += thing[status].min;
          }
          return null;
        })
      );
    thingName = things[0].name;
    // console.log(thingName)
    return [result, thingName];
  }, [things]);

  // useEffect 用于触发 setState
  useEffect(() => {
    // 满足条件时, 设置共享状态值(物品的统计属性)
    if (things.find((element) => element.name === "Scroll")) {
      setState((state) => ({ ...state, [thingName]: result }));
      // console.log("result",result)
    }
  }, [thingName]);

  // 绘制属性
  const renderResult = (status) => {
    if (result[status].max !== 0) {
      if (result[status].min !== result[status].max) {
        return (
          <span>
            {status}: {result[status].min}~{result[status].max}
          </span>
        );
      } else {
        return (
          <span>
            {status}: {result[status].min}
          </span>
        );
      }
    }
  };

  return (
    <div className={styles.status}>
      <img className={styles.icon} src={things[0].icon} alt="" />
      {status.map((status, key) => {
        return <div key={key}>{renderResult(status)}</div>;
      })}
    </div>
  );
}
