import React, { useContext, useEffect } from "react";
import ReactDOM from "react-dom";
import styles from "./Maker.scss";
import { MyStatusContext } from "./index.jsx";
import meso from "./image/meso.png";
import rippleEffect from '../../utils/ripple-min';


export default function Maker(props) {
  const { thing } = props;
  const [state] = useContext(MyStatusContext);
  const status = ["att", "matt", "str", "dex", "int", "luk"];

  const renderMaker = (status) => {
    // console.log(JSON.stringify(state));
    if (
      Object.keys(state).length !== 0 &&
      state[thing.name][status].max !== 0
    ) {
      return (
        <span>
          {status}: {state[thing.name][status].max}
        </span>
      );
    }
  };

  const water =()=> { 
    // console.log(document.getElementById('root'));
    rippleEffect(document.getElementById('root'))};

  return (
    <div className={styles.main}>
      <ul className={styles.status}>
        <img className={styles.icon} src={meso} alt="" />
        {status.map((status, key) => {
          return <li key={key}>{renderMaker(status)}</li>;
        })}
      </ul>
      <a onClick={water}>
        <span className={styles.myspan}></span>
        <span className={styles.myspan}></span>
        <span className={styles.myspan}></span>
        <span className={styles.myspan}></span>&nbsp;GAMBLE?&nbsp;
      </a>
    </div>
  );
}
